import { BuildContext, Button, Component, ComponentState, Container, getServerActionsClient, Heading, Row, StatefulComponent, Text, TextInput } from "@equantic/runtime";

export class Counter extends StatefulComponent {
    createState() {
        return new CounterState(this)
    }

    async incrementOnServer(current: number) {
        return await getServerActionsClient().invoke('Counter/IncrementOnServer', [current])
    }

}


export class CounterState extends ComponentState {
    _component: Counter;
    _needsRender: boolean = false;
    _count: number = 0;
    _message: string = "";
    constructor(component: Counter) {
        super();
        this._component = component;
    }

    setState(fn: () => void) {
        fn();
        this._needsRender = true;
        this._component._scheduleRender();
    }

    _increment(): void { return this.setState(() => this._count++); }

    _decrement(): void { return this.setState(() => this._count--); }

    build(context: BuildContext) {
        return (
        new Container({

            id: 'counter-container',
            className: 'counter',
            dataAttributes: { testid: 'counter' },
            children: [

                new Heading({

                    content: 'eQuantic.UI Counter',
                    className: 'title',
                }
                )
                ,
                new TextInput({

                    id: 'message-input',
                    value: this._message,
                    placeholder: 'Type something...',
                    onChange: (value) => this.setState(() => this._message = value),
                    ariaAttributes: { label: 'Message input' },
                }
                )
                ,
                new Row({

                    gap: '8px',
                    justify: JustifyContent.Center,
                    children: [

                        new Button({

                            id: 'decrement-btn',
                            className: 'btn btn-secondary',
                            onClick: this._decrement,
                            text: '-',
                        }
                        )
                        ,
                        new Text({

                            content: `${this._count}`,
                            className: 'count-display',
                        }
                        )
                        ,
                        new Button({

                            id: 'increment-btn',
                            className: 'btn btn-primary',
                            onClick: this._increment,
                            text: '+',
                        }
                        )
                        ,
                    ]

                }
                )
                ,
            ]

        }
        )
        );
    }

}


